* {
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

:root {
  --transparent-gray: #80808040;
  --primary-blue: #a78bfa;
  --secondary-blue: #a78bfab3;
  --primary-black: #000000b3;
  --secondary-black: #0000004d;
  --primary-pink: #9b5bf5;
  --white: #fff;
}

html {
  scroll-behavior: smooth;
}

body {
  z-index: 1;
}

a {
  color: inherit;
  text-decoration: none !important;
}

.icon {
  contain: strict;
  fill: currentColor;
  width: 1em;
  height: 1em;
  display: inline-block;
  box-sizing: content-box !important;
}

.section {
  color: var(--white);
  height: 100%;
  margin: 9rem 0;
}

.section-title {
  letter-spacing: .08em;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(#b741dd 47.2%, #7d00f2 90.1%);
  -webkit-background-clip: text;
  text-transform: uppercase;
  -webkit-background-clip: text;
  background-clip: text;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto 2rem;
  font-size: 2.6rem;
  line-height: 3.2rem;
  font-family: Montserrat, sans-serif !important;
}

.go-to-top-btn {
  z-index: 9;
  cursor: pointer;
  background: none;
  border-radius: 50%;
  padding: 1.3rem;
  transition-property: all;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(0, 0, .2, 1);
  display: none;
  position: fixed;
  bottom: 30px;
  right: 20px;
  transform: rotateZ(-45deg);
  box-shadow: 0 0 #0000, 0 0 #0000, 0 1px 3px #0000001a, 0 1px 2px -1px #0000001a;
  border: none !important;
  outline: none !important;
}

.go-to-top-btn .show {
  display: block;
}

.rocketSmoke {
  width: 100px;
  height: 100px;
  rotate: 46deg;
}

.rocketFire {
  width: 100px;
  height: 100px;
  display: none;
  rotate: 46deg;
}

@media only screen and (max-width: 768px) {
  .go-to-top-btn {
    bottom: 0;
    right: -8%;
  }

  .section {
    margin: 5rem 0;
  }

  .section-title {
    text-align: center;
    width: 90%;
    font-size: 1.8rem !important;
    line-height: 2.5rem !important;
  }
}

.rocket-btn {
  color: var(--secondary-blue);
  text-shadow: none;
  font-size: 30px;
  transition: all .4s;
  animation: 2s infinite wave;
}

.go-to-top-btn:hover .rocket-btn {
  color: var(--primary-blue);
  text-shadow: 10px 10px 80px var(--primary-blue);
}

@keyframes wave {
  0% {
    transform: translateY(0%);
  }

  25% {
    transform: translateY(-2%);
  }

  50% {
    transform: translateY(-4%);
  }

  75% {
    transform: translateY(-2%);
  }

  100% {
    transform: translateY(0%);
  }
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background-image: radial-gradient(at 10% 20%, #000 0%, #020022 81.3%);
}

::-webkit-scrollbar-thumb {
  opacity: .7;
  background: linear-gradient(#fff0 0%, #a78bfa 35%, #fff0 100%);
}

.opaque8 {
  opacity: .8;
}

